<template>
  <div>
    <a-card>
      <h3>{{ id ? '' : '' }}</h3>
      <a-form-model :model="artInfo" ref="artInfoRef" :rules="artInfoRules">
        <a-form-model-item label="" prop="title">
          <a-input style="width: 300px;" v-model="artInfo.title"></a-input>
        </a-form-model-item>
        <a-form-model-item label="" prop="cid">
          <a-select v-model="artInfo.cid" style="width: 200px;" placeholder="" @change="cateChange">
            <a-select-option v-for="item in cateList" :key="item.id" :value="item.id">{{ item.name }}</a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="" prop="desc">
          <a-input type="textarea" v-model="artInfo.desc"></a-input>
        </a-form-model-item>
        <a-form-model-item label="" prop="img">
          <a-upload
            :multiple="false"
            :action="upUrl"
            :headers="headers"
            list-type="picture"
            :defaultFileList="fileList"
            @change="upChange"
          >
            <a-button>
              <a-icon type="upload"></a-icon>
            </a-button>
          </a-upload>
          <br />
          <template v-if="id && artInfo.img">
            <img :src="artInfo.img" alt="" style="width: 200px; height: 180px; display: inline-block; float: left">
          </template>
        </a-form-model-item>
        <a-form-model-item label="" prop="content">
          <Editor v-model="artInfo.content"></Editor>
        </a-form-model-item>
        <a-form-model-item>
          <a-button type="danger" style="margin-right: 20px;" @click="artOk(artInfo.id)">{{ artInfo.id ? '' : '' }}</a-button>
          <a-button type="primary" @click="cancel"></a-button>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>
<script>
import { Url } from '../../plugin/http'
import Editor from '../editor/index'
export default {
  components: { Editor },
  props: ['id'],
  data () {
    return {
      artInfo: {
        id: 0,
        title: '',
        cid: undefined,
        desc: '',
        content: '',
        img: ''
      },
      cateList: [],
      upUrl: Url + '/upload',
      headers: {},
      fileList: [],
      artInfoRules: {
        title: [{ required: true, message: '', trigger: 'blur' }],
        cid: [{ required: true, message: '', trigger: 'change' }],
        desc: [{ required: true, message: '', trigger: 'blur' }, { max: 120, message: '120', trigger: 'change' }],
        img: [{ required: true, message: '', trigger: 'blur' }],
        content: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created () {
    this.getCateList()
    this.headers = {
      Authorization: `Bearer ${window.sessionStorage.getItem('token')}`
    }
    if (this.id) {
      this.getArtInfo(this.id)
    }
  },
  methods: {
    async getArtInfo (id) {
      const { data: res } = await this.$http.get(`article/${id}`)
      if (res.status !== 200) return this.$message.error(res.message)
      this.artInfo = res.data
      this.artInfo.id = res.data.ID
    },
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.status !== 200) return this.$message.error(res.message)
      this.cateList = res.data
    },
    // 
    cateChange (id) {
      this.artInfo.cid = id
    },
    // 
    upChange (info) {
      if (info.file.status !== 'uploading') {
      }
      if (info.file.status === 'done') {
        this.$message.success(`${info.file.name} `)
        this.artInfo.img = info.file.response.url
      } else if (info.file.status === 'error') {
        this.$message.error(`${info.file.name} `)
      }
    },
    // 
    artOk (id) {
      this.$refs.artInfoRef.validate(async (valid) => {
        if (!valid) {
          return this.$message.error('')
        }
        if (id === 0) {
          // 
          const { data: res } = await this.$http.post('article/add', this.artInfo)
          if (res.status !== 200) return this.$message.error(res.message)
          this.$message.success('')
          await this.$router.push('/artlist')
        } else {
          const { data: res } = await this.$http.put(`article/${id}`, this.artInfo)
          if (res.status !== 200) return this.$message.error(res.message)
          this.$message.success('')
          await this.$router.push('/artlist')
        }
      })
    },
    cancel () {
      this.$refs.artInfoRef.resetFields()
    }
  }
}
</script>
