<template>
  <div>
    <h3></h3>
    <a-card>
      <a-row :gutter="20">
        <a-col :span="6">
          <a-input-search placeholder="" enter-button v-model="queryUserName" @search="getUserList" allowClear></a-input-search>
        </a-col>
        <a-col :span="4">
          <a-button type="primary" @click="addUser"></a-button>
        </a-col>
      </a-row>
      <a-table
        rowKey="username"
        :columns="columns"
        :pagination="paginationOption"
        :dataSource="userlist"
        bordered
      >
        <span slot="role" slot-scope="role">{{ role === 1 ? '' : '' }}</span>
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="primary" icon="edit" style="margin-right: 15px" @click="editUser(data.ID)"></a-button>
            <a-button type="danger" icon="delete" style="margin-right: 15px" @click="deleteUser(data.ID, data.role)"></a-button>
            <a-button type="info" icon="info" @click="resetPassword(data.ID)"></a-button>
          </div>
        </template>
      </a-table>
    </a-card>
    <!--    -->
    <a-modal
      title=""
      closable
      :visible="addUserVisible"
      @ok="addUserHandleOk"
      @cancel="addUserHandleCancel"
    >
      <a-form-model :model="newUserInfo" :rules="newUserRules" ref="addUserRef">
        <a-form-model-item label="" prop="username">
          <a-input v-model="newUserInfo.username"></a-input>
        </a-form-model-item>
        <a-form-model-item has-feedback label="" prop="password">
          <a-input-password v-model="newUserInfo.password"></a-input-password>
        </a-form-model-item>
        <a-form-model-item has-feedback label="" prop="checkPassword">
          <a-input-password v-model="newUserInfo.checkPassword"></a-input-password>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
    <!--    -->
    <a-modal
      title=""
      closable
      :visible="editUserVisible"
      @ok="editUserHandleOk"
      @cancel="editUserHandleCancel"
    >
      <a-form-model :model="userInfo" :rules="userRules" ref="editUserRef">
        <a-form-model-item label="" prop="username">
          <a-input v-model="userInfo.username"></a-input>
        </a-form-model-item>
        <a-form-model-item label="" prop="role">
          <a-switch :checked="userInfo.role === 1" @change="adminChange"></a-switch>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
    <!--    -->
    <a-modal
      title=""
      closable
      :visible="resetPasswordVisible"
      @ok="resetPasswordHandleOk"
      @cancel="resetPasswordHandleCancel"
    >
      <a-form-model :model="resetPasswordInfo" :rules="resetPasswordRules" ref="resetPasswordRef">
        <a-form-model-item has-feedback label="" prop="password">
          <a-input-password v-model="resetPasswordInfo.password"></a-input-password>
        </a-form-model-item>
        <a-form-model-item has-feedback label="" prop="checkPassword">
          <a-input-password v-model="resetPasswordInfo.checkPassword"></a-input-password>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
  </div>
</template>

<script>
const columns = [
  {
    title: 'ID',
    dataIndex: 'ID',
    width: '10%',
    key: 'id'
  },
  {
    title: '',
    dataIndex: 'username',
    width: '20%',
    key: 'username'
  },
  {
    title: '',
    dataIndex: 'role',
    width: '20%',
    key: 'role',
    scopedSlots: { customRender: 'role' }
  },
  {
    title: '',
    width: '30%',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]
export default {
  data () {
    return {
      queryUserName: '',
      paginationOption: {
        pageSizeOptions: ['5', '10', '20'],
        defaultCurrent: 1,
        defaultPageSize: 5,
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => `${total}`,
        onChange: (page, pageSize) => {
          this.getUserList(pageSize, page)
          this.paginationOption.defaultCurrent = page
          this.paginationOption.defaultPageSize = pageSize
        },
        onShowSizeChange: (current, size) => {
          this.getUserList(size, current)
          this.paginationOption.defaultCurrent = current
          this.paginationOption.defaultPageSize = size
        }
      },
      userlist: [],
      columns,
      // add user
      addUserVisible: false,
      newUserInfo: {
        id: 0,
        username: '',
        password: '',
        checkPassword: '',
        role: 2
      },
      newUserRules: {
        username: {
          validator: (rule, value, callback) => {
            if (this.newUserInfo.username === '') {
              callback(new Error(''))
            }
            if (this.newUserInfo.username.length < 4 || this.newUserInfo.username.length > 12) {
              callback(new Error('412'))
            } else {
              callback()
            }
          },
          trigger: 'blur'
        },
        password: [
          {
            validator: (rule, value, callback) => {
              if (this.newUserInfo.password === '') {
                callback(new Error(''))
              }
              if (this.newUserInfo.password.length < 6 || this.newUserInfo.password.length > 20) {
                callback(new Error('620'))
              } else {
                callback()
              }
            },
            trigger: 'blur'
          }
        ],
        checkPassword: [
          {
            validator: (rule, value, callback) => {
              if (this.newUserInfo.password === '') {
                callback(new Error(''))
              }
              if (this.newUserInfo.password !== this.newUserInfo.checkPassword) {
                callback(new Error(''))
              } else {
                callback()
              }
            },
            trigger: 'blur'
          }
        ]
      },
      // edit user
      editUserVisible: false,
      userInfo: {
        id: 0,
        username: '',
        role: 2
      },
      userRules: {
        username: {
          validator: (rule, value, callback) => {
            if (this.userInfo.username === '') {
              callback(new Error(''))
            }
            if (this.userInfo.username.length < 4 || this.userInfo.username.length > 12) {
              callback(new Error('412'))
            } else {
              callback()
            }
          },
          trigger: 'blur'
        }
      },
      resetPasswordVisible: false,
      resetPasswordInfo: {
        id: 0,
        password: '',
        checkPassword: ''
      },
      resetPasswordRules: {
        password: [
          {
            validator: (rule, value, callback) => {
              if (this.resetPasswordInfo.password === '') {
                callback(new Error(''))
              }
              if (this.resetPasswordInfo.password.length < 6 || this.resetPasswordInfo.password.length > 20) {
                callback(new Error('620'))
              } else {
                callback()
              }
            },
            trigger: 'blur'
          }
        ],
        checkPassword: [
          {
            validator: (rule, value, callback) => {
              if (this.resetPasswordInfo.password === '') {
                callback(new Error(''))
              }
              if (this.resetPasswordInfo.password !== this.resetPasswordInfo.checkPassword) {
                callback(new Error(''))
              } else {
                callback()
              }
            },
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created () {
    this.getUserList(this.queryUserName, this.paginationOption.defaultPageSize, this.paginationOption.defaultCurrent)
  },
  methods: {
    async getUserList (pageSize, pageNum) {
      const { data: res } = await this.$http.get('users', {
        params: {
          username: this.queryUserName,
          pagesize: pageSize,
          pagenum: pageNum
        }
      })
      if (res.status !== 200) return this.$message.error(res.message)
      this.userlist = res.data
      this.paginationOption.total = res.total
    },
    // 
    async deleteUser (id, role) {
      this.$confirm({
        title: '',
        content: '',
        onOk: async () => {
          if (role === 1) {
            this.$message.error('')
            return
          }
          const res = await this.$http.delete(`user/${id}`)
          if (res.status !== 200) return this.$message.error(res.message)
          this.$message.success('')
          await this.getUserList()
        },
        onCancel: () => {
          this.$message.info('')
        }
      })
    },
    // 
    addUser () {
      this.addUserVisible = true
    },
    addUserHandleOk () {
      this.$refs.addUserRef.validate(async (valid) => {
        if (!valid) {
          return this.$message.error('')
        }
        const { data: res } = await this.$http.post('user/add',
          {
            username: this.userInfo.username,
            password: this.userInfo.password,
            role: this.userInfo.role
          }
        )
        if (res.status !== 200) return this.$message.error(res.message)
        this.addUserVisible = false
        this.$message.success('')
        await this.getUserList()
      })
    },
    addUserHandleCancel () {
      this.$refs.addUserRef.resetFields()
      this.addUserVisible = false
    },
    adminChange (check) {
      if (check) {
        this.userInfo.role = 1
      } else {
        this.userInfo.role = 2
      }
    },
    // 
    async editUser (id) {
      this.editUserVisible = true
      const { data: res } = await this.$http.get(`user/${id}`)
      this.userInfo = res.data
      this.userInfo.id = id
    },
    editUserHandleOk () {
      this.$refs.editUserRef.validate(async (valid) => {
        if (!valid) return this.$message.error('')
        const { data: res } = await this.$http.put(`user/${this.userInfo.id}`, {
          username: this.userInfo.username,
          role: this.userInfo.role
        })
        if (res.status !== 200) return this.$message.error(res.message)
        this.editUserVisible = false
        this.$message.success('')
        await this.getUserList()
      })
    },
    editUserHandleCancel () {
      this.$refs.editUserRef.resetFields()
      this.editUserVisible = false
    },
    // 
    resetPassword (id) {
      this.resetPasswordInfo.id = id
      this.resetPasswordVisible = true
    },
    resetPasswordHandleOk () {
      this.$refs.resetPasswordRef.validate(async (valid) => {
        if (!valid) return this.$message.error('')
        const { data: res } = await this.$http.put(`reset/${this.resetPasswordInfo.id}`, {
          password: this.resetPasswordInfo.password
        })
        if (res.status !== 200) return this.$message.error(res.message)
        this.resetPasswordVisible = false
        this.$message.success('')
        await this.getUserList()
      })
    },
    resetPasswordHandleCancel () {
      this.$refs.resetPasswordRef.resetFields()
      this.resetPasswordVisible = false
    }
  }
}
</script>

<style scoped>
  .actionSlot {
    display: flex;
    justify-content: center;
  }
</style>
